<template>
  <div>
    App displayed
      <navbar @change-display="displayItem" @decrement="decrement" @increment="increment"></navbar>
      <itemDisplay :selecteditem="debug.selectedItem"></itemDisplay>
  </div>
</template>

<script>
//import HelloWorld from './components/HelloWorld.vue'
import navbar from './components/navbar.vue'
import itemDisplay from './components/itemDisplay.vue'

export default {
  name: 'App',
  components: {
    navbar, itemDisplay
  },
  data() {
        return {
            debug: {
                selectedItem: 0
            }
        }
    },
    methods: {
        displayItem(index) {
            this.debug.selectedItem = index
            console.log(index)
        },
        decrement() {
            if (this.debug.selectedItem > 0) {
                this.debug.selectedItem--
            }
        },
        increment(length) {
            if (this.debug.selectedItem < length-1) {
                this.debug.selectedItem++
            }
        }
    }
}
</script>
